<template lang="pt-br">
    <NavBar/>
    <div class="body">
        <h1>Seja bem vindo ao <span>Harmonic Game!</span></h1>
        <h2>Escolha a escala que deseja praticar:</h2>
        <div class="content" >
            <router-link class="router" @click="notaPressionada" :to="{name:'escala'}">
                <div class="notas"  v-for="nota in notas" :key="nota">
                    {{nota}}
                </div>
            </router-link>
           
        </div>
    </div>
</template>

<script>

import NavBar from "./NavBar.vue";
export default {
    data(){
        return{
            notas:['C', 'D', 'E', 'F', 'G', 'A', 'B'],
            nota: ''
        }
    },
    components:{
    NavBar
  },
    methods:{
        notaPressionada(e) {
            //Pega a nota pressionada e manda para o componente ExEscala
            let _nota = e.target.innerHTML
            this.nota = _nota 
            localStorage.setItem('nota', _nota)

        }
    }
}
</script>
