 @import url('https://fonts.googleapis.com/css2?family=Bungee+Inline&family=Rubik+Vinyl&display=swap'); 
 @import url('https://fonts.googleapis.com/css2?family=Bungee+Inline&family=Chau+Philomene+One&family=Rubik+Vinyl&display=swap'); 
 .body{
